"use client";

import React, { useEffect, useState } from "react";
import { Box, Image, Text, Button, VStack, HStack } from "@chakra-ui/react";
import { motion, AnimatePresence } from "framer-motion";
import axios from "axios";

interface UserSuggestion {
  id: number;
  name: string;
  age: number;
  location: string;
  bio: string;
  image: string;
}

const MotionBox = motion(Box);

const MainHome = () => {
  const [user, setUser] = useState<UserSuggestion | null>(null);
  const [swipeDirection, setSwipeDirection] = useState<"left" | "right" | null>(
    null
  );

  // Fetch user data from the backend
  const fetchUser = async () => {
    try {
      const response = await axios.get("/api/user/suggestion");
      setUser(response.data);
    } catch (error) {
      console.error("Error fetching user suggestion:", error);
    }
  };

  useEffect(() => {
    fetchUser();
  }, []);

  const handleSwipe = (direction: "left" | "right") => {
    setSwipeDirection(direction);
    setTimeout(() => {
      fetchUser();
      setSwipeDirection(null);
    }, 500); // Delay to allow swipe animation
  };

  if (!user) {
    return <Text>Loading suggestions...</Text>;
  }

  return (
    <VStack spacing={4} align="center" mt={8}>
      <AnimatePresence>
        {user && (
          <MotionBox
            key={user.id}
            initial={{ opacity: 0, x: 0 }}
            animate={{ opacity: 1, x: 0 }}
            exit={{
              x: swipeDirection === "left" ? -300 : 300,
              opacity: 0,
            }}
            transition={{ duration: 0.5 }}
            bg="gray.700"
            borderRadius="md"
            overflow="hidden"
            boxShadow="lg"
            width="350px"
            height="500px"
          >
            <Image
              src={user.image}
              alt={`${user.name}'s profile`}
              objectFit="cover"
              width="100%"
              height="70%"
            />
            <VStack p={4} align="start">
              <Text fontSize="lg" fontWeight="bold">
                {user.name}, {user.age}
              </Text>
              <Text fontSize="sm" color="gray.400">
                {user.location}
              </Text>
              <Text fontSize="sm" noOfLines={1}>
                {user.bio.length > 50 ? `${user.bio.substring(0, 47)}...` : user.bio}
              </Text>
            </VStack>
          </MotionBox>
        )}
      </AnimatePresence>

      <HStack spacing={4}>
        <Button
          colorScheme="red"
          onClick={() => handleSwipe("left")}
          w="100px"
        >
          Ignore
        </Button>
        <Button
          colorScheme="green"
          onClick={() => handleSwipe("right")}
          w="100px"
        >
          Like
        </Button>
        <Button colorScheme="blue" w="100px">
          Chat
        </Button>
      </HStack>
    </VStack>
  );
};

export default MainHome;
